*{
  box-sizing: border-box;
}

body{
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: cursive;
  background: linear-gradient(to right, #2F80ED, #56CCF2);
 }

.content{
  width: 500px;
  height: 580px;
  padding: 0.5rem;
  background: #fff;
  border-radius: 3px;
  text-align: center;
 }

.form{
  margin: auto;
  max-width: 460px;
  width: 430px;
  height: 50px;
  background: #f0f0f0;
  border-radius: 4px;
  display: grid;
  grid-template-columns: 80% 20%;
  padding: 0 0 0 1.2rem;
}

.form input{
  outline: none;
  border: none;
  background: none;
}

.form button{
  color: #fff;
  border: none;
  cursor: pointer;
  background: #56CCF2;
}

.card{
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}

.card li{
  width: 415px;
  margin: 10px auto;
  padding: 20px 10px;
  background: aliceblue;
  font-family: Calibri;
  border-radius: 4px;
}

li span{
  padding: 10px;
}

li button{
  float: right;
  cursor: pointer;
  border: none;
  padding: 3px 8px;
  color: #fff;
  font-weight: bold;
  margin: 0 5px;
  font-size: small;
}

.remove{background: #FF0844;}
.edit{background: #00C6FB; padding: 3px 10px;}

.completed{
  text-decoration: line-through;
  color: #ccc;
}